@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.clear-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.clear-paragraph {
  margin: 0;
}
.clear-link {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page {
  width: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
}
.page__body {
  min-width: 320px;
  width: 100%;
  height: 100vh;
  margin: auto;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  color: #444444;
  background-color: #FFFFFF;
}
.img-map {
  object-fit: cover;
}
.main-title {
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #000000;
}
.main-title--smaller {
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
}
.main-title--bigger-line-height {
  line-height: 40px;
}
.main-title--white {
  color: #FFFFFF;
}
.main-text {
  font-family: "Arial", sans-serif;
  color: #444444;
}
.main-text--smaller {
  font-size: 12px;
  line-height: 12px;
  text-transform: lowercase;
}
.main-nav {
  display: flex;
  flex-direction: column;
  padding: 20px;
  z-index: 1;
}
@media (min-width: 768px) {
  .main-nav {
    flex-direction: row;
    justify-content: space-between;
    padding: 24px 30px 0;
    align-items: flex-end;
  }
}
@media (min-width: 1220px) {
  .main-nav {
    padding: 53px 110px 0;
  }
}
.main-nav__mobile-nav {
  display: flex;
  width: 280px;
  justify-content: space-between;
  align-items: center;
}
.main-nav__logo {
  display: block;
  padding: 0;
  margin: 0;
  font-size: 0;
  line-height: 0;
}
.main-nav__logo:hover {
  opacity: 0.8;
}
.main-nav__logo:active {
  opacity: 0.6;
}
.main-nav__logo picture {
  padding: 0;
  margin: 0;
  display: block;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
    min-width: 440px;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}
.main-nav__button {
  display: none;
}
@media (max-width: 767px) {
  .main-nav--no-js .main-nav__button {
    display: none;
  }
  .main-nav__button {
    display: inline-block;
    width: 24px;
    height: 18px;
    padding: 0;
    border: none;
  }
  .main-nav__button--open {
    background: transparent url("../img/logo-mobile--open.png") no-repeat 0 0;
  }
  .main-nav__button--close {
    background: transparent url("../img/logo-mobile--close.png") no-repeat 0 0;
  }
  .main-nav__list--hidden {
    display: none;
  }
  .main-nav--no-js .main-nav__list--hidden {
    display: block;
  }
}
.main-nav__item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .main-nav__item {
    border-top: none;
    height: 36px;
    align-items: flex-start;
  }
}
.main-nav__link {
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  color: #000000;
}
@media (min-width: 768px) {
  .main-nav__link {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}
@media (min-width: 1220px) {
  .main-nav__link {
    color: #FFFFFF;
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
  }
}
.main-nav__link--current {
  border-bottom: 2px solid #68B738;
}
@media (min-width: 1220px) {
  .main-nav__link--current {
    border-bottom: 2px solid #FFFFFF;
  }
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
