  }
}
@media (min-width: 768px) {
  .process__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 280px;
    column-gap: 60px;
    row-gap: 37px;
    counter-reset: numb;
  }
}
@media (min-width: 1220px) {
  .process__list {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 80px;
  }
}
.process__item {
  display: grid;
  grid-template-columns: 60px 1fr;
  grid-template-rows: 60px;
  align-items: center;
  gap: 20px;
  position: relative;
  margin: 10px 0;
  counter-increment: numb;
}
@media (min-width: 768px) {
  .process__item {
    display: block;
    padding: 150px 0 57px;
  }
}
@media (min-width: 768px) {
  .process__item::after {
    content: counter(numb);
    font-size: 280px;
    line-height: 240px;
    color: #f2f2f2;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
  }
}
.process__item::before {
  content: "";
  width: 60px;
  height: 60px;
  background-position: center;
}
@media (min-width: 768px) {
  .process__item::before {
    position: absolute;
    top: 20px;
    left: 0;
    width: 80px;
    height: 80px;
  }
}
.process__item:first-child::before {
  background-image: url("../img/index/Group.png");
}
.process__item:nth-child(2)::before {
