  .contacts__wrapper {
    position: absolute;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 80px;
    top: 95px;
    left: 110px;
    width: 570px;
    height: 201px;
    background-color: #FFFFFF;
    padding: 66px 76px 52px;
  }
}
.contacts__text {
  color: #000000;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .contacts__text {
    font-size: 20px;
    line-height: 21px;
  }
}
.contacts__address {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .contacts__address {
    font-size: 16px;
  }
}
.main-footer {
  display: grid;
  grid-template-rows: 95px 63px 78px;
  align-items: center;
  text-align: center;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .main-footer {
    padding: 0 30px;
    align-items: center;
    grid-template-columns: 128px 1fr 163px;
    grid-template-rows: 150px;
  }
}
.main-footer__social {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.main-footer__logo:hover {
  opacity: 0.8;
}
.main-footer__logo:active {
  opacity: 0.6;
}
.main-footer__social-link {
  margin: 0 10px;
  color: #666666;
}
.main-footer__social-link:hover {
  color: #68B738;
}
.main-footer__social-link:active {
  opacity: 0.3;
  color: #68B738;
}
@media (min-width: 768px) {
  .main-footer__social-link--vk svg {
    width: 30px;
    height: 22px;
  }
}
@media (min-width: 768px) {
  .main-footer__social-link--insta svg {
    width: 23px;
    height: 23px;
  }
}
@media (min-width: 768px) {
  .main-footer__social-link--facebook svg {
    width: 28px;
    height: 33px;
    fill: #000000;
  }
}
.main-footer__copyright-link {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
}
